@import url("https://fonts.googleapis.com/css2?family=PingFang+SC:wght@400;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Helvetica+Neue:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=SF+UI+Text:wght@400&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-pingfang-sc: "PingFang SC";
  --font-helvetica-neue: "Helvetica Neue";
  --font-sf-ui-text: "SF UI Text";

  /* font sizes */
  --font-size-sm: 14px;
  --font-size-base: 16px;

  /* Colors */
  --color-gray-100: #fafafa;
  --color-gray-200: #001529;
  --color-gray-300: rgba(0, 0, 0, 0.85);
  --color-gray-400: rgba(0, 0, 0, 0.45);
  --color-gray-500: rgba(0, 0, 0, 0.65);
  --color-white: #fff;
  --color-dodgerblue: #1890ff;
  --color-gainsboro: #e8e8e8;
  --color-limegreen: #52c41a;

  /* Border radiuses */
  --br-9xs: 4px;
  --br-11xs: 2px;
}
